@font-face {
  font-family: 'Pacifico';
  src: local('Pacifico'), url(./fonts/Pacifico-Regular.ttf) format('truetype');
}

@font-face {
  font-family: 'Bungee';
  src: local('Bungee'), url(./fonts/Bungee-Regular.ttf) format('truetype');
}
  
@font-face {
    font-family: 'BungeeShade';
    src: local('BungeeShade'), url(./fonts/BungeeShade-Regular.ttf) format('truetype');
  }

  @font-face {
    font-family: 'Arimo';
    src: local('Arimo'), url(./fonts/Arimo-Regular.ttf) format('truetype');
  }



body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
